.parallax {
    -webkit-perspective: 1px;
            perspective: 1px;
    background-image: url(/portfolio_react_01/static/media/IMG_3491.c59d98eb.JPG);
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    position: relative;
    width: 100vw;
}

.parallax-small {
  -webkit-perspective: 1px;
          perspective: 1px;
  background-image: url(/portfolio_react_01/static/media/IMG_3491.c59d98eb.JPG);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 40vh;
  position: relative;
  /* width: 100vw; */
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.parallax > p {
    font-size: 30px;
    background-color: rgba(119, 141, 142, 0.70);
    border-radius: 15px;
    padding: 18px;
    color:aliceblue;
    position: absolute;
    text-align: center;
    display: inline-block;
    background-color: #e3ede975;
    width: 70%;
    margin: 10px;
    -webkit-justify-content: center;
            justify-content: center;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }

.parallax-small > p {
    grid-column: 2/-2;
    font-size: 30px;
    background-color: rgba(119, 141, 142, 0.70);
    border-radius: 15px;
    padding: 18px;
    color:aliceblue;
    position: absolute;
    text-align: center;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }

