.bg-modal {
  width: 100%;
  height: 200vh;
  background-color: #00000070;
  position: absolute;
  top: 0;
  /* display: flex; */
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  display: none;

}

.modal-content {
  width: 80%;
  height: unquote("calc(100vh - 20px)");
  position: -webkit-sticky;
  position: sticky;
  background-color: white;
  /* text-align: center; */
  padding: 20px;
  position: relative;
  border-radius: 4px;
}

.modal-close {
  color:black;
  position: absolute;
  background-color: transparent;
  border: none;
  top: 0;
  right: 8px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  font-size: 32px;
  cursor: pointer;
}

